import { Observable, Subject } from 'rxjs';
import { Injectable } from '@angular/core';
@Injectable({ providedIn: 'root' })
export class BillpayNotificationRoutingService {
  private _upcommingPaymentsSubject = new Subject<undefined>();
  upcommingPaymentsSubject$: Observable<undefined> = this._upcommingPaymentsSubject.asObservable();
  private _managePaymentSubject = new Subject<string>();
  managePaymentSubject$: Observable<string> = this._managePaymentSubject.asObservable();
  private _managePayeesSubject = new Subject<undefined>();
  managePayeesSubject$: Observable<undefined> = this._managePayeesSubject.asObservable();

  navigateToPayments() {
    this._upcommingPaymentsSubject.next();
  }

  navigateToNewPayment(payeeId: string) {
    this._managePaymentSubject.next(payeeId);
  }

  navigateToPayees() {
    this._managePayeesSubject.next();
  }
}
